.contacts {
  padding-bottom: 25px;
  &__flex {
    display: flex;
    align-items: start;
    gap: 100px;
    margin-top: 60px;
  }
  .form {
    width:  calc(100% - 750px);
  }
  &__items {
    width: 650px;
    display: grid;
    grid-template-columns: 100%;
    gap: 30px;
  }
  &__item {
    display: flex;
    align-items: start;
    gap: 30px;
    &-icon {
      width: 34px;
      flex-shrink: 0;
      img {
        max-width: 100%;
        margin-top: 5px;
      }
    }
    &-title {
      font-size: 19px;
      line-height: 160%;
      font-weight: 700;
    }
    &-text {
      font-size: 19px;
      line-height: 160%;
    }
  }
  &__phone,
  &__email {
    &:hover {
      opacity: 0.7;
    }
  }
  .map {
    margin-top: 40px;
  }
}

@media screen and (max-width: 1600px) {
  .contacts {
    &__flex {
      gap: 60px;
    }
    .form {
      width: 60%;
    }
    &__items {
      width: 40%;
    }
  }
}

@media screen and (max-width: 1400px) {
  .contacts {
    &__item {
      &-title {
        font-size: 16px;
      }
      &-text {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 1200px) {
  .contacts {
    .form {
      width: 50%;
    }
    &__items {
      width: 50%;
    }
  }
}

@media screen and (max-width: 992px) {
  .contacts {
    &__title {
      text-align: center;
    }
    &__flex {
      flex-direction: column;
      gap: 40px;
      margin-top: 40px;
    }
    .form {
      width: 100%;
    }
    &__items {
      width: 100%;
      order: -1;
    }
    .map {
      margin-top: 30px;
    }
  }
}

@media screen and (max-width: 576px) {
  .contacts {
    &__item {
      &-text {
        br {
          display: none;
        }
      }
    }
  }
}