.about-main {
  padding-bottom: 25px;
  &__title {
    margin-bottom: 80px;
  }
  &__subtitle {
    font-size: 24px;
    line-height: 150%;
    font-weight: 700;
    margin-bottom: 40px;
  }
  &__text {
    p {
      font-size: 19px;
      line-height: 120%;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: repeat(5, calc(20% - 72px / 5));
    gap: 18px;
    margin-top: 60px;
  }
  &__item {
    img {
      width: 100%;
    }
  }
  &__items2 {
    display: grid;
    grid-template-columns: 100%;
    gap: 60px;
    margin-top: 60px;
  }
  &__item2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 25px;
    &-img {
      img {
        width: 100%;
      }
    }
    .about-main__desc {
      max-width: 700px;
    }
  }
  .map {
    margin-top: 60px;
  }
}

@media screen and (max-width: 1400px) {
  .about-main {
    &__title {
      margin-bottom: 60px;
    }
    &__subtitle {
      font-size: 20px;
    }
    &__text {
      p {
        font-size: 16px;
      }
    }
    &__items {
      margin-top: 40px;
    }
    &__item2 {
      .about-main__desc {
        max-width: 500px;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .about-main {
    &__title {
      text-align: center;
      margin-bottom: 35px;
    }
    &__subtitle {
      text-align: center;
      margin-bottom: 35px;
    }
    &__items {
      grid-template-columns: repeat(4, calc(25% - 30px / 4));
      gap: 10px;
    }
    &__item {
      &:last-child {
        grid-column: 3/5;
        grid-row: 1/3;
      }
    }
    &__items2 {
      gap: 40px;
      margin-top: 40px;
    }
    &__item2 {
      &-img {
        width: 50%;
      }
      .about-main__desc {
        width: 50%;
        max-width: 100%;
      }
      .about-main__subtitle {
        text-align: start;
      }
    }
    .map {
      margin-top: 40px;
    }
  }
}

@media screen and (max-width: 768px) {
  .about-main {
    &__items {
      grid-template-columns: repeat(2, calc(50% - 10px / 2));
    }
    &__item {
      &:last-child {
        grid-column: 1/3;
        grid-row: unset;
      }
    }
    &__item2 {
      flex-direction: column;
      &-img {
        width: 100%;
      }
      .about-main__desc {
        width: 100%;
        order: -1;
      }
    }
  }
}