.wrapper {
  position: relative;
  padding: 15px;
  overflow: hidden;
}
.btn-white__outline {
  position: relative;
  background: transparent;
  border: 2px solid #fff;
  border-radius: 16px;
  color: #fff;
  &:hover {
    background: #fff;
    color: #000;
    .btn-arrow {
      svg path {
        fill: #000;
      }
    }
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
  .btn-arrow {
    position: absolute;
    z-index: 2;
    top: 10px;
    right: 10px;
    width: 18px;
    svg {
      width: 100%;
      height: auto;
    }
    svg path {
      transition: all 0.3s;
    }
  }
}
.btn-orange {
  background: #FFC621;
  border-radius: 10px;
  color: #fff;
  &:hover {
    background: #ffbb00;
  }
  span {
    line-height: 100%;
    font-weight: 500;
  }
}
.h1 {
  font-size: 64px;
  line-height: 100%;
  font-weight: 500;
}
.h2 {
  font-size: 50px;
  line-height: 100%;
  font-weight: 500;
}
.swiper-button-prev,
.swiper-button-next {
  width: 75px;
  height: 75px;
  background: transparent;
  border: 3px solid #E9E9E9;
  border-radius: 50%;
  opacity: 1 !important;
  &:after {
    display: none;
  }
  &:hover {
    background: #E9E9E9;
  }
  svg {
    max-width: 50%;
    width: auto;
    height: auto;
  }
}
.swiper-pagination-progressbar {
  top: unset !important;
  bottom: 0;
  width: 100% !important;
  height: 6px !important;
  background: #F0F0F0;
  border-radius: 30px;
  &-fill {
    background: #131313 !important;
    border-radius: 30px;
  }
}
.breadcrumbs {
  padding: 65px 0 50px;
  &__items {
    display: flex;
    flex-wrap: wrap;
    gap: 10px 5px;
  }
  &__item {
    font-size: 18px;
    line-height: 100%;
    font-weight: 500;
    &:hover {
      opacity: 0.7;
    }
    &--active:hover {
      opacity: 1;
    }
  }
}
.section-top {
  &__group {
    max-width: 1013px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    margin-top: 100px;
  }
  &__logo {
    width: 208px;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__text {
    max-width: 662px;
    p {
      font-size: 19px;
      line-height: 160%;
    }
  }
}
.form {
  border: 1px solid #CADAEC;
  border-radius: 10px;
  padding: 25px;
  &-top {
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    gap: 12px;
  }
  &-grid {
    width: 320px;
    display: grid;
    grid-template-columns: 100%;
    gap: 6px;
  }
  &-group {
    position: relative;
    &--big {
      width: calc(100% - 332px);
      textarea {
        width: 100%;
        height: 100%;
      }
    }
    input {
      width: 100%;
      height: 62px;
      background: #F5F7F9;
      border: 1px solid transparent;
      border-radius: 10px;
      padding: 0 24px;
      font-size: 16px;
      line-height: 100%;
      color: #252626;
      &::placeholder {
        color: #252626 !important;
      }
    }
    textarea {
      background: #F5F7F9;
      border: 1px solid transparent;
      border-radius: 10px;
      padding: 24px;
      font-size: 16px;
      line-height: 100%;
      color: #252626;
      resize: none;
      &::placeholder {
        color: #252626 !important;
      }
    }
    .placeholder {
      position: absolute;
      z-index: 2;
      top: 50%;
      left: 0;
      transform: translate(0, -50%);
      padding: 0 24px;
      font-size: 16px;
      line-height: 100%;
      color: #252626;
      pointer-events: none;
      span {
        color: #D0232B;
      }
    }
  }
  &-bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 16px;
    margin-top: 16px;
  }
  &-check {
    position: relative;
    max-width: 350px;
    input {
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      padding: 0;
      margin: 0;
      opacity: 0;
    }
    input:checked ~ label:before {
      background-image: url("../img/check.svg");
    }
    label {
      position: relative;
      display: block;
      padding-left: 30px;
      font-size: 12px;
      line-height: 100%;
      font-weight: 300;
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 21px;
        height: 21px;
        background-repeat: no-repeat;
        background-position: center;
        background-size: auto;
        border: 2px solid #D0232B;
        border-radius: 5px;
      }
      a {
        position: relative;
        z-index: 3;
      }
      a:hover {
        opacity: 0.7;
      }
    }
  }
  &-btn {
    min-width: 350px;
    height: 60px;
    flex-shrink: 0;
    padding: 5px 40px;
    span {
      font-size: 16px;
    }
  }
}
.map {
  position: relative;
  height: 500px;
  .ymaps-2-1-79-map {
    border-radius: 16px;
  }
  .ymaps-2-1-79-inner-panes {
    border-radius: 16px;
  }
}

@media screen and (max-width: 1400px) {
  .h2 {
    font-size: 40px;
  }
  .form {
    &-top {
      flex-direction: column;
      gap: 6px;
    }
    &-grid {
      width: 100%;
    }
    &-group--big {
      width: 100%;
      textarea {
        height: 186px;
      }
    }
    &-btn {
      min-width: unset;
    }
  }
}

@media screen and (max-width: 1200px) {
  .wrapper {
    padding: 0;
  }
}

@media screen and (max-width: 992px) {
  .h1 {
    font-size: 36px;
  }
  .h2 {
    font-size: 32px;
  }
  .breadcrumbs {
    padding: 30px 0;
  }
  .section-top {
    &__group {
      flex-direction: column;
      text-align: center;
      gap: 50px;
      margin-top: 30px;
    }
    &__logo {
      width: 165px;
    }
    &__text {
      max-width: 360px;
      p {
        font-size: 14px;
        line-height: 120%;
      }
    }
  }
  .form {
    padding: 20px 15px;
    &-group {
      input {
        height: 45px;
        border-radius: 7.5px;
        padding: 0 15px;
        font-size: 12px;
      }
      textarea {
        border-radius: 7.5px;
        padding: 15px;
        font-size: 12px;
      }
      .placeholder {
        font-size: 12px;
        padding: 0 15px;
      }
    }
    &-group--big {
      textarea {
        height: 112px;
      }
    }
    &-btn {
      height: 45px;
      span {
        font-size: 12px;
      }
    }
  }
  .breadcrumbs {
    &__items {
      justify-content: center;
      align-items: center;
    }
  }
}

@media screen and (max-width: 576px) {
  .breadcrumbs {
    &__item {
      font-size: 14px;
    }
  }
  .form {
    &-bottom {
      flex-direction: column;
      align-items: start;
      gap: 10px;
    }
    &-btn {
      width: 100%;
      order: -1;
    }
    &-check {
      max-width: 260px;
      label {
        padding-left: 20px;
        font-size: 9px;
        &:before {
          width: 15px;
          height: 15px;
          border-width: 1px;
          background-size: 8px 6px;
        }
      }
    }
  }
  .map {
    .ymaps-2-1-79-map {
      border-radius: 0;
    }
    .ymaps-2-1-79-inner-panes {
      border-radius: 0;
    }
  }
}

@media screen and (max-width: 359px) {
  .swiper-button-prev,
  .swiper-button-next {
    width: 50px;
    height: 50px;
  }
}