.projects-main {
  padding-bottom: 60px;
  .section-top {
    margin-bottom: 115px;
  }
  .about__item {
    &-img {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        background: linear-gradient(180.00deg, rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.8) 100%);
        pointer-events: none;
      }
    }
    @media screen and (min-width: 1401px) {
      &-title {
        font-size: 40px;
      }
      &-text {
        max-width: 992px;
        p {
          font-size: 19px;
        }
      }
    }
    &-title {
      font-weight: 700;
    }
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    margin-top: 40px;
  }
}

@media screen and (max-width: 1400px) {
  .projects-main {
    .section-top {
      margin-bottom: 60px;
    }
  }
}

@media screen and (max-width: 992px) {
  .projects-main {
    padding-bottom: 25px;
    &__subtitle {
      text-align: center;
    }
  }
}

@media screen and (max-width: 576px) {
  .projects-main {
    .about__item {
      &-img {
        height: auto;
        display: block;
        &:after {
          display: none;
        }
        img {
          height: auto;
        }
      }
      &-desc {
        position: static;
        padding: 0;
        margin-top: 35px;
      }
      &-title {
        font-size: 20px;
        color: #000;
      }
      &-text {
        display: block;
        margin-top: 25px;
        p {
          font-size: 14px;
          color: #000;
        }
      }
    }
  }
}