.layouts-main {
  padding-bottom: 25px;
  .layouts__slider {
    margin-top: 130px;
  }
  .form {
    width: calc(100% - 702px);
  }
  .contacts__flex {
    gap: 40px;
    align-items: center;
    margin-top: 90px;
    .section-top__group {
      width: 662px;
      flex-direction: column;
      align-items: start;
      gap: 60px;
      margin: 0;
    }
  }
}

@media screen and (max-width: 1600px) {
  .layouts-main {
    .layouts__slider {
      margin-top: 50px;
    }
    .form {
      width: 60%;
    }
    .contacts__flex {
      .section-top__group {
        width: 40%;
      }
    }
  }
}

@media screen and (max-width: 992px) {
  .layouts-main {
    .form {
      width: 100%;
    }
    .contacts__flex {
      gap: 25px;
      .section-top__group {
        width: 100%;
        order: -1;
        align-items: center;
      }
    }
  }
}